





































import { Component, Vue, Emit, Prop } from 'vue-property-decorator';
import { RaidenError } from 'raiden-ts';

import ActionButton from '@/components/ActionButton.vue';
import { Token } from '@/model/types';
import RaidenDialog from '@/components/RaidenDialog.vue';
import ErrorMessage from '@/components/ErrorMessage.vue';
import { BalanceUtils } from '../utils/balance-utils';

@Component({
  components: { ActionButton, RaidenDialog, ErrorMessage }
})
export default class MintDepositDialog extends Vue {
  @Prop({ required: true })
  token!: Token;

  @Prop({ required: true, type: Boolean })
  visible!: boolean;

  loading: boolean = false;
  error: Error | RaidenError | null = null;
  amount: string = '1';

  @Emit()
  cancel() {
    this.error = null;
  }

  async mint() {
    this.error = null;
    this.loading = true;

    try {
      await this.$raiden.mint(
        this.token.address,
        BalanceUtils.parse(this.amount, this.token.decimals!)
      );
      this.$emit('done');
    } catch (e) {
      this.error = e;
    }

    this.loading = false;
  }
}
